<template>
  <div id="classPractice">
    <head-top title="历史课程"></head-top>
    <div class="main">
      <div class="hint">
        <div class="title">2019年高级英语第一课时练习</div>
        <div class="info">
          <div class="item time">
            总分：
            <span>100分</span>
          </div>
          <div class="item time">
            题数：
            <span>20题</span>
          </div>
          <div class="item time">
            时间：
            <span>2019-06-01 10:00</span>
          </div>
        </div>
      </div>

      <div class="content">
        <div class="title">1.【单选题】5分</div>
        <div class="info">1.【单选题】5分</div>
        <div class="selects">
          <div class="select">A：进程控制块</div>
          <div class="select">B：文件控制块</div>
          <div class="select">C：设备控制块</div>
          <div class="select">D：作业控制块</div>
        </div>
        <div class="answer">
          答案：
          <span>A</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  components: {},
  props: {},
  data() {
    return {};
  },
  computed: {},
  watch: {},
  created() {},
  methods: {}
};
</script>

<style lang='less'>
#classPractice {
  width: 100%;
  height: 100%;
  text-align: left;
  .main {
    width: 100%;
    .hint {
      width: 100%;
      height: 7vh;
      overflow: hidden;
      padding: 5px;
      background: rgba(255, 255, 255, 1);
      .title {
        width: 100vw;
        margin-bottom: 10px;
        background: rgba(255, 255, 255, 1);
      }
      .info {
        width: 100vw;

        display: flex;
        justify-content: space-between;
        span {
          color: black;
        }
        .item {
          &:last-child {
            width: 40%;
          }
        }
      }
    }
  }
}
</style>


